        buffetModel: BuffetTimeSpan,
        consumeTypes: [],
        dishes: [],
        activeConsumeTypeIndex: null,
        setting: defaultSetting,
      }
    },
    computed: {
      filter: function () {
        return this.activeConsumeTypeIndex ? { consumeTypeId: this.activeConsumeTypeIndex } : null
      },
    },
    async mounted () {
      this.consumeTypes = await ConsumeType.getList()
      console.log(this.consumeTypes)
      this.dishes = await Dish.getList()
    },
